import React from "react";
import { Route } from "react-router-dom";

import BuildingSitePart1 from "../../data/posts/9-14-2023-building-site-part-1.mdx";
import BuildingSitePart2 from "../../data/posts/9-14-2023-building-site-part-2.mdx";

import { Header1, Header2, Paragraph, Pre, Code, Link } from "./styles";

const components = {
  h1: Header1,
  h2: Header2,
  p: Paragraph,
  img: Image,
  pre: Pre,
  code: Code,
  a: Link,
};

export default function useBlogRoutes() {
  return (
    <>
      <Route
        path="/blog/post/building-my-site-part-1"
        element={<BuildingSitePart1 components={components} />}
      />
      <Route
        path="/blog/post/building-my-site-part-2"
        element={<BuildingSitePart2 components={components} />}
      />
    </>
  );
}
